import React, { useContext } from 'react';
import Symbol from '../Symbol';
import { CurrentSymbolContext } from '../../Contexts/CurrentSymbol';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';
const SymbolList = ({ symbols }) => {
	const [ currentSymbol, setCurrentSymbol ] = useContext(CurrentSymbolContext);
	const history = useHistory();
	const selectHandler = (symbol) => {
		setCurrentSymbol(symbol);
		history.push('/pick');
	};

	return (
		<div>
			<SymbolGrid>
				{symbols.map((symbol) => <Symbol key={symbol.id} symbol={symbol} onSelect={selectHandler} />)}
			</SymbolGrid>
		</div>
	);
};

const SymbolGrid = styled.div`
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	align-items: center;
	justify-content: center;
	gap: 2rem;
`;

export default SymbolList;
