import React, { createContext, useState } from 'react';

export const CurrentSymbolContext = createContext();

export const CurrentSymbolProvider = (props) => {
	const [ currentSymbol, setCurrentSymbol ] = useState(null);
	return (
		<CurrentSymbolContext.Provider value={[ currentSymbol, setCurrentSymbol ]}>
			{props.children}
		</CurrentSymbolContext.Provider>
	);
};
