import React from 'react';
import styled from 'styled-components';

const Symbol = ({ symbol, onSelect }) => {
	return (
		<SymbolCard>
			<h3>{symbol.name}</h3>
			<SymbolImage src={symbol.path} alt={symbol.name} />
			{onSelect && <button onClick={() => onSelect(symbol)}>select</button>}
		</SymbolCard>
	);
};

const SymbolCard = styled.div`
	display: flex;
	flex-direction: column;
	background: white;
	border-radius: 10%;
`;

const SymbolImage = styled.img`
	display: block;
	height: 200px;
	object-fit: contain;
`;

export default Symbol;
