import React, { useContext } from 'react';
import { useForm } from 'react-hook-form';
import { CartContext } from '../Contexts/CartContext';
import Axios from 'axios';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';

const CheckoutPage = () => {
	const { register, handleSubmit } = useForm();
	const [ state, dispatch ] = useContext(CartContext);
	const { items } = state;
	const history = useHistory();

	const sendOrder = async (client, cartItems) => {
		try {
			console.log({
				client,
				items: cartItems
			});
			const res = await Axios.post('http://127.0.0.1:8000/api/new-order', {
				client,
				items: cartItems
			});
			console.log(res);
		} catch (error) {
			alert(error);
		}
	};

	const onSumbit = (client) => {
		try {
			const res = sendOrder(client, items);
			dispatch({ type: 'CLEAR_CART' });
			history.push('/praise');
			console.log(res);
		} catch (error) {}
	};
	return (
		<div>
			<form onSubmit={handleSubmit(onSumbit)}>
				<input
					type="text"
					name="first_name"
					id="firstName"
					ref={register({ required: true })}
					placeholder="First name"
				/>
				<input
					type="text"
					name="last_name"
					id="lastName"
					ref={register({ required: true })}
					placeholder="Last name"
				/>
				<input type="email" name="email" id="email" ref={register({ required: true })} placeholder="email" />
				<input type="submit" title="submit" />
			</form>
			{items.map((item) => (
				<div>
					<h2>{item.product}</h2>
					<h2>{item.color}</h2>
					<h2>{item.symbol.name}</h2>
				</div>
			))}
		</div>
	);
};

export default CheckoutPage;
