import React, { useContext } from 'react';
import { CurrentSymbolContext } from '../Contexts/CurrentSymbol';
import { useForm } from 'react-hook-form';
import styled from 'styled-components';
import { CartContext } from '../Contexts/CartContext';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';

const PickProductPage = () => {
	const [ currentSymbol ] = useContext(CurrentSymbolContext);
	const { register, handleSubmit } = useForm({
		quantity: '1'
	});
	const [ state, dispatch ] = useContext(CartContext);
	const history = useHistory();
	const onSumbit = (data) => {
		dispatch({ type: 'ADD_ITEM', payload: { ...data, symbol: { ...currentSymbol } } });
		console.log(state);
		history.push('/');
	};
	return (
		<div>
			{currentSymbol && <img src={currentSymbol.path} alt={currentSymbol.name} />}
			<CheckoutForm onSubmit={handleSubmit(onSumbit)}>
				<div>
					<input type="radio" name="product" value="tshirt" id="tshirt" ref={register({ required: true })} />
					<label htmlFor="tshirt">T-shirt</label>
					<input type="radio" name="product" value="cup" id="cup" ref={register({ required: true })} />
					<label htmlFor="cup">Cup</label>
				</div>
				<div>
					<input type="radio" name="color" value="blue" id="blue" ref={register({ required: true })} />
					<label htmlFor="blue">Blue</label>
					<input type="radio" name="color" value="yellow" id="yellow" ref={register({ required: true })} />
					<label htmlFor="yellow">Yellow</label>
					<input type="radio" name="color" value="white" id="white" ref={register({ required: true })} />
					<label htmlFor="white">White</label>
				</div>

				<input name="quantity" type="number" ref={register({ required: true })} defaultValue={1} />
				<input type="submit" title="Add to card" />
			</CheckoutForm>
		</div>
	);
};

const CheckoutForm = styled.form`
	display: flex;
	flex-direction: column;
	max-width: 50%;
`;

export default PickProductPage;
