import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import Axios from 'axios';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';

const LoginPage = () => {
	const { register, handleSubmit } = useForm();
	const [ error, setError ] = useState('');
	const history = useHistory();
	const onSumbit = async ({ login, pass }) => {
		const res = await Axios.post('http://127.0.0.1:8000/api/login', {
			login,
			pass
		});
		console.log(res);
		const { allowed } = res.data;
		if (!allowed) {
			setError('Wrong password or login.');
			return;
		}
		history.push('/admin');
	};
	return (
		<div>
			{error ? error : null}
			<form onSubmit={handleSubmit(onSumbit)}>
				<input type="text" name="login" id="login" ref={register({ required: true })} />
				<input type="password" name="pass" id="password" ref={register({ required: true })} />
				<input type="submit" value="Login" />
			</form>
		</div>
	);
};

export default LoginPage;
