import React from 'react';
import HomePage from './pages/HomePage';
import { CartContextProvider } from './Contexts/CartContext';
import { CurrentSymbolProvider } from './Contexts/CurrentSymbol';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import PickProductPage from './pages/PickProductPage';
import Header from './components/Header';
import CartPage from './pages/CartPage';
import CheckoutPage from './pages/CheckoutPage';
import PraisePage from './pages/PraisePage';
import LoginPage from './pages/LoginPage';
import AdminPage from './pages/AdminPage';
function App() {
	return (
		<Router>
			<div>
				<CartContextProvider>
					<CurrentSymbolProvider>
						<Header />
						<Switch>
							<Route exact path="/">
								<HomePage />
							</Route>
							<Route path="/pick">
								<PickProductPage />
							</Route>
							<Route path="/cart">
								<CartPage />
							</Route>
							<Route path="/checkout">
								<CheckoutPage />
							</Route>
							<Route path="/praise">
								<PraisePage />
							</Route>
							<Route path="/login">
								<LoginPage />
							</Route>
							<Route path="/admin">
								<AdminPage />
							</Route>
						</Switch>
					</CurrentSymbolProvider>
				</CartContextProvider>
			</div>
		</Router>
	);
}

export default App;
